export const series = (data) => {
  let yoLo = data?.data?.map((item, index) => {
    return [new Date(item[0]).getTime(), Number(item[1])];
  });
  return [
    {
      name: data?.name,
      yaxis: [
        {
          min: data?.yaxis?.[0].min,
          max: data?.yaxis?.[0].max,
        },
      ],

      data: yoLo,
    },
  ];
};

