import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navbar from "../ManDashboard/components/Navbar/Navbar";
import Footer from "../ManDashboard/components/Footer/Footer";
import { getSensorDataById } from "services/sensor";

import BlueGraph from "../ManDashboard/components/BlueGraph/BlueGraph";
import { Button, Flex } from "@chakra-ui/react";
import { useNavigate } from "react-router-dom";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import "./index.css";
import { FaCalendar } from "react-icons/fa";
export default function GraphPage({}) {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
   const CustomInput = ({ value, onClick }) => (
     <div className="custom-input">
       <input
         type="text"
         value={value}
         onClick={onClick}
         placeholder="Select a date"
       />
       <FaCalendar onClick={onClick} className="calendar-icon" />
     </div>
   );
  const onFormSubmit = (e) => {
    e.preventDefault();
  };
  const location = useLocation();
  const navi = useNavigate();
  const [sensorData, setSensorsData] = useState();
  const [loading, setLoading] = useState(true);
  const [isSensorId, setIsSensorId] = useState();
  useEffect(() => {
    let sensorId = location?.search?.split("&")[0].split("=")[1];
    let sensorName = location?.search?.split("&")[1]?.split("=")[1];
    sensorName = decodeURIComponent(sensorName);
    if (sensorName == "undefined") {
      sensorName = sensorId;
    }
    setIsSensorId(sensorName);
    setLoading(true);
    const utcstartDate = startDate.toISOString();
     const utcEndDate = endDate.toISOString();
    getSensorDataById(sensorId, sensorName, utcstartDate ,utcEndDate)
      .then((res) => {
        setLoading(false);
        setSensorsData(res?.data);
      })
      .catch((err) => {
        setLoading(false);
        console.error(err);
      });
  }, [startDate, endDate]);

  const handleButtonClick = () => {
    const storedSystemId = localStorage.getItem("systemId");
    const storedThemeColor = localStorage.getItem("themeColor");
    const params = new URLSearchParams();
    params.set("systemId", storedSystemId);
    params.set("themeColor", storedThemeColor);
    navi(`/?${params.toString()}`);
  };

  return (
    <div>
      <Flex flexDirection="column" p={{ base: "10px", md: "20px" }}>
        <Button w="20" onClick={handleButtonClick}>
          Back
        </Button>
        <form onSubmit={onFormSubmit}>
          <div className="form-group">
            <div className="DatePicker">
              <div>
                <div>
                  <p style={{ fontWeight: "bold" }}>Select a Start Date</p>
                </div>
                <DatePicker
                  selected={startDate}
                  onChange={(date) => setStartDate(date)}
                  name="startDate"
                  dateFormat="MM/dd/yyyy"
                  placeholderText="Select start date"
                  customInput={<CustomInput />}
                />
              </div>
              <div>
                <div>
                  <p style={{ fontWeight: "bold" }}>Select a End Date</p>
                </div>
                <DatePicker
                  selected={endDate}
                  onChange={(date) => setEndDate(date)}
                  name="endDate"
                  dateFormat="MM/dd/yyyy"
                  placeholderText="Select end date"
                  customInput={<CustomInput />}
                />
              </div>
            </div>
          </div>
        </form>
        <div>
          <BlueGraph
            sensorId={isSensorId}
            sensorsData={sensorData}
            loading={loading}
          />
        </div>
      </Flex>
    </div>
  );
}
