import React from "react";
import { Flex, useColorModeValue, useColorMode } from "@chakra-ui/react";

import "./status.css";
function Status({ textColor, bgColor, icon, text, value }) {
  const { colorMode } = useColorMode();
  const dynamicBackgroundColor =
    colorMode === "light" ? bgColor : useColorModeValue("#fff", "#1E293B");

  const statusCardStyle = {
    backgroundColor: dynamicBackgroundColor,
    color: textColor,
  };

  return (
    <div className="status__card" style={statusCardStyle}>
      <div className="status">
        <h1 className="status__count">{value}</h1>
        <div className="status__name">
          <img src={icon} alt="status icon" />
          <span>{text}</span>
        </div>
      </div>
    </div>
  );
}

export default Status;
