import React from "react";
import { Table, Tag } from "antd";
import "./NotificationTable.css";

const columns = [
  {
    title: "Raised",
    dataIndex: "Raised",
    key: "name",
    render: (text) => <a>{text}</a>,
  },
  {
    title: "Device",
    dataIndex: "Device",
    key: "age",
  },

  {
    title: "Severity",
    key: "tags",
    dataIndex: "Severity",
    render: (_, { Severity }) => (
      <>
        {Severity.map((tag) => {
          let color = tag.length > 5 ? "geekblue" : "green";
          if (tag === "Alram") {
            color = "red";
          }
          return (
            <Tag color={color} key={tag}>
              {tag.toUpperCase()}
            </Tag>
          );
        })}
      </>
    ),
  },
];
