export const getSensorsData = async () => {
  const urlParams = new URLSearchParams(window.location.search);
  const systemId = urlParams.get("systemId");
  if (!systemId) {
    const res = await request.get("AWS/GetSensorsData");
    return res;
  }
  const res = await request.get(`AWS/GetSensorsData?departmentId=${systemId}`);
  return res;
};
getSensorsData();

