// getSensorsDataHCCAP(data);
export const getSensorsDataHCCAP = async ({ date, day }) => {
  var now_utc = date.toISOString();
  const urlParams = new URLSearchParams(window.location.search);
  const systemId = urlParams.get("systemId");
  if (!systemId) {
    const res = await request.get(
      `/AWS/GetAllSensorsData?DepartmentId=24&NoOfDays=${day}&Interval=3&SelectedDate=${now_utc}`
    );
    console.log("data>>>>>", res);
    return res;
  }
  const res = await request.get(
    `/AWS/GetAllSensorsData?DepartmentId=${systemId}&NoOfDays=${day}&Interval=3&SelectedDate=${now_utc}`
  );
  console.log("data>>>>>", res);
  return res;
};
export const getSensorDataById = async (sensorId, sensorName, startDate , endedDate) => {
  const res = await request.get(
    `/AWS/GetSensorDataById?SensorId=${sensorId}&SesnsorName=${sensorName}&SelectedDate=${startDate}&EndedDate=${endedDate}`
  );
  return res;
};
