import React, { useState, useEffect } from "react";
import { Table } from "antd";
import { getSensorsDataHCCAP } from "services/sensor";

const ReportTable = () => {
  const [loading, setLoading] = useState("false");
  const [hccpColumns, setHccpColumns] = useState([]);
  const [hccpData, setHccpData] = useState([]);
  useEffect(() => {
    setLoading(true);
    getSensorsDataHCCAP()
      .then((res) => {
        setLoading(false);
        let columm = ["Sensor Name", ...res?.data?.[0]?.columns];

        setHccpColumns(
          columm?.map((item, index) => {
            return {
              title: item === "Sensor Name" ? item : `${item}:00`,
              dataIndex: item === "Sensor Name" ? "title" : item,
            };
          })
        );
        res?.data.shift();
        setHccpData(res?.data);
      })
      .catch((err) => {
        setLoading(false);
      });
  }, []);
  return (
    <>
      <Table columns={hccpColumns} dataSource={hccpData} loading={loading} />
    </>
  );
};
export default ReportTable;
