export const donutChartDataCharts1 = (maxValue, minValue, currentValue) => {
  if (maxValue < 0 && minValue < 0 && currentValue < 0) {
    return [calculatePercentage(-minValue, -maxValue, -currentValue)];
  } else {
    return [calculatePercentage(minValue, maxValue, currentValue)];
  }
};
