export function getTimeDifference(currentTime, previousTime) {
  // Convert previousTime to a Date object
  previousTime = new Date(previousTime);

  // Convert currentTime to a UTC timestamp
  currentTime = Date.UTC(
    currentTime.getUTCFullYear(),
    currentTime.getUTCMonth(),
    currentTime.getUTCDate(),
    currentTime.getUTCHours(),
    currentTime.getUTCMinutes(),
    currentTime.getUTCSeconds()
  );

  // Convert previousTime to a UTC timestamp
  const previousTimestamp = Date.UTC(
    previousTime.getUTCFullYear(),
    previousTime.getUTCMonth(),
    previousTime.getUTCDate(),
    previousTime.getUTCHours(),
    previousTime.getUTCMinutes(),
    previousTime.getUTCSeconds()
  );

  const timeDifference = currentTime - previousTimestamp;

  const seconds = Math.floor(timeDifference / 1000);
  const minutes = Math.floor(seconds / 60);
  const hours = Math.floor(minutes / 60);
  const days = Math.floor(hours / 24);

  if (days > 0) {
    return `${days} day${days > 1 ? "s" : ""} ago`;
  } else if (hours > 0) {
    return `${hours} hour${hours > 1 ? "s" : ""} ago`;
  } else if (minutes > 0) {
    return `${minutes} minute${minutes > 1 ? "s" : ""} ago`;
  } else {
    return `${seconds} second${seconds !== 1 ? "s" : ""} ago`;
  }
}
