import React, { useState } from "react";
import "react-grid-layout/css/styles.css";
import "react-resizable/css/styles.css";
import "./GridStack.css";
import { Box, Text, IconButton, Button, Flex, VStack } from "@chakra-ui/react";
import { Responsive as ResponsiveGridLayout } from "react-grid-layout";
import { withSize } from "react-sizeme";
import { FaFileAlt, FaChartBar, FaBell } from "react-icons/fa";
import ReportTable from "../ReportTable/ReportTable";
import Donut from "../DonutCharts/Donut";
import Widget from "./Widget";
import TopBar from "./Topbar";
import NotificationTable from "../NotificationTable/NotificationTable";
import { Switch } from "@chakra-ui/react";
import { FaPencilAlt } from "react-icons/fa";
const GridStackComponent = ({ size: { width }, isOn }) => {
  const originalItems = ["a", "b", "c"];

  const initialLayouts = {
    lg: [
      { i: "a", x: 0, y: 0, w: 1, h: 2 },
      { i: "b", x: 1, y: 0, w: 3, h: 2 },
      { i: "c", x: 4, y: 0, w: 1, h: 2 },
      { i: "d", x: 0, y: 2, w: 2, h: 2 },
    ],
  };

  const componentList = {
    a: Donut,
    b: ReportTable,
    c: NotificationTable,
    d: Donut,
  };
  const [items, setItems] = useState(originalItems);
  const [layouts, setLayouts] = useState(
    getFromLS("layouts") || initialLayouts
  );
  const onLayoutChange = (_, allLayouts) => {
    setLayouts(allLayouts);
  };
  const onLayoutSave = () => {
    saveToLS("layouts", layouts);
  };
  const onRemoveItem = (itemId) => {
    setItems(items.filter((i) => i !== itemId));
  };
  const onAddItem = (itemId) => {
    setItems([...items, itemId]);
  };
  const onAddDynamicItem = () => {
    const newItemId = `dynamic-${Math.random()}`;
    setItems([...items, newItemId]);
    setLayouts((prevLayouts) => ({
      ...prevLayouts,
      lg: [
        ...prevLayouts.lg,
        { i: newItemId, x: 0, y: 0, w: 2, h: 4 }, // You can adjust these values
      ],
    }));
  };
  const onAddComponent = () => {
    const newLayout = {
      i: Date.now().toString(), // Generate a unique ID for the new component
      x: 0,
      y: Infinity, // Puts the component at the bottom
      w: 2,
      h: 4,
    };

    setLayouts((prevLayouts) => [...prevLayouts, newLayout]);
  };
  return (
    <div>
      <Flex alignItems={"center"}>
        <div className="editWidgets">
          {isOn && (
            <>
              <TopBar
                onLayoutSave={onLayoutSave}
                items={items}
                onRemoveItem={onRemoveItem}
                onAddItem={onAddItem}
                originalItems={originalItems}
              />
            </>
          )}
        </div>
      </Flex>
      <ResponsiveGridLayout
        className="layout"
        layouts={layouts}
        breakpoints={{ lg: 1200, md: 996, sm: 768, xs: 480, xxs: 0 }}
        cols={{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 }}
        rowHeight={60}
        width={width}
        onLayoutChange={onLayoutChange}
      >
        {items.map((key, index) => (
          <div
            key={`${key}-${index}`} // Use both key and index to ensure uniqueness
            className="widget"
            data-grid={{ w: 8, h: 8, x: 0, y: Infinity }}
          >
            <Widget
              id={key}
              onRemoveItem={onRemoveItem}
              backgroundColor="#867ae9"
              ComponentList={componentList[key]}
            />
          </div>
        ))}
      </ResponsiveGridLayout>
    </div>
  );
};

export default withSize({ refreshMode: "debounce", refreshRate: 60 })(
  GridStackComponent
);

function getFromLS(key) {
  let ls = {};
  if (localStorage) {
    try {
      ls = JSON.parse(localStorage.getItem("rgl-8")) || {};
    } catch (e) {}
  }
  return ls[key];
}

function saveToLS(key, value) {
  if (localStorage) {
    localStorage.setItem(
      "rgl-8",
      JSON.stringify({
        [key]: value,
      })
    );
  }
}
