import { Flex, Box, Text, Divider, useColorModeValue } from "@chakra-ui/react";
import Card from "components/Card/Card";
import CardBody from "components/Card/CardBody";
import DonutChart from "components/Charts/DonutChart";
import Battery from "assets/svg/Battery.svg";
import React, { useState, useEffect } from "react";
import {
  donutChartDataCharts1,
  donutChartOptionsCharts1,
} from "variables/DonutData";
import "./donut.css";
import { getTimeDifference } from "variables/DonutData";
import { Skeleton } from "antd";
import { useNavigate } from "react-router-dom";
import Gauge from "../Gauge/Gauge";
import { getSensorsData } from "services/sensor";
function Donut() {
  const [sensorData, setSensorData] = useState();
  const [loadingDonut, setLoadingDonut] = useState(false);

  const sensorListing = () => {
    setLoadingDonut(true);
    getSensorsData()
      .then((res) => {
        setLoadingDonut(false);
        let data = res?.data?.map((item) => {
          return {
            ...item,
            temperature: Number(item?.temperature?.[0]),
          };
        });
        setSensorData(data);
      })
      .catch((err) => {
        setLoadingDonut(false);
      });
  };
  useEffect(() => {
    sensorListing();
    const interval = setInterval(() => {
      sensorListing();
    }, 120000);

    return () => clearInterval(interval);
  }, []);
  const hour = useColorModeValue("#64748B", "white");
  const batteryColor = useColorModeValue("black", "white");
  const navi = useNavigate();
  return (
    <div className="card-top">
      <Flex flexWrap="wrap" style={{ gap: "50px", flexWrap: "wrap" }} mb={10}>
        {loadingDonut ? (
          <>
            <Card
              w={{
                base: "100%",
                sm: "100%",
                md: "calc(50% - 10px)",
                lg: "calc(25% - 15px)",
              }}
              backgroundColor={useColorModeValue("white", "#1E293B")}
              cursor={"pointer"}
            >
              <CardBody w="100%" h="100%">
                <Skeleton active paragraph={{ rows: 3 }} />
              </CardBody>
              <Box className="max-min"></Box>
              <Divider />
              <Box className="reading-footer"></Box>
            </Card>
            <Card
              w={{
                base: "100%",
                sm: "100%",
                md: "calc(50% - 10px)",
                lg: "calc(25% - 15px)",
              }}
              backgroundColor={useColorModeValue("white", "#1E293B")}
              cursor={"pointer"}
            >
              <CardBody w="100%" h="100%">
                <Skeleton active paragraph={{ rows: 3 }} />
              </CardBody>
              <Box className="max-min"></Box>
              <Divider />
              <Box className="reading-footer"></Box>
            </Card>
          </>
        ) : (
          sensorData?.map((item, index) => (
            <Card
              key={index}
              w={{
                base: "100%",
                sm: "100%",
                md: "calc(50% - 10px)",
                lg: "calc(25% - 15px)",
              }}
              onClick={() => {
                navi(
                  `/admin/show-graph?sensorId=${item.sensorId}&sensorName=${item.sensorName}`
                );
              }}
              backgroundColor={useColorModeValue("white", "#1E293B")}
              cursor={"pointer"}
            >
              <Box className="reading-header">
                <Text>
                  {item?.sensorName ? item.sensorName : item.sensorId}
                </Text>
              </Box>
              <CardBody w="100%" h="100%">
                <Gauge
                  minValue={item?.minValue}
                  maxValue={item?.maxValue}
                  currentValue={isNaN(item?.value) ? 0 : item?.value}
                />
              </CardBody>
              <Box className="max-min">
                <Flex className="min" direction="row">
                  <Text className="min-heading">Min</Text>
                  <Text>{item.minValue}</Text>
                </Flex>
                <Flex className="max" direction="row">
                  <Text className="max-heading">Max</Text>
                  <Text>{item.maxValue}</Text>
                </Flex>
              </Box>
              <Divider />
              <Box className="reading-footer">
                <Text className="hours" color={hour}>
                  {getTimeDifference(new Date(), item?.readingDateTime)}
                </Text>
                <Flex className="battery">
                  <img
                    src={Battery}
                    alt="Battery"
                    style={{
                      width: "20px",
                      height: "20px",
                      marginLeft: "5px",
                      // filter:
                      //   item.batteryValue < 3.5
                      //     ? "brightness(0) saturate(100%)"
                      //     : "brightness(0) saturate(0%)",
                    }}
                  />
                  <Text
                    style={{ color: item.batteryValue < 3.5 ? "red" : "green" }}
                  >
                    {item.batteryValue}
                  </Text>
                </Flex>
              </Box>
              {/* <Flex className="battery" align="center">
                <img src={Battery} alt="" style={{ color: "#64748b" }} />
                <Text className="percentage">{item?.battery}</Text>
              </Flex> */}
            </Card>
          ))
        )}
      </Flex>
    </div>
  );
}

export default Donut;
