import React from "react";
import GaugeChart from "react-gauge-chart";
import "./Gauge.css";
import { calculatePercentage } from "variables/DonutData";
const Gauge = ({ minValue, maxValue, currentValue, }) => {
  const safeCurrentValue = Math.min(Math.max(currentValue, minValue), maxValue);
  // const percentage = (currentValue - minValue) / (maxValue - minValue);
  const percentage = Math.min(
    Math.max((safeCurrentValue - minValue) / (maxValue - minValue), 0),1);
  const redToYellow = generateGradientColor("#ca0035", "#FFD700", 5);
  const yellowToGreen = generateGradientColor("#FFD700", "#2ECC71", 8);
  const greenToYellow = [...yellowToGreen].reverse();
  const yellowToRed = [...redToYellow].reverse();
  const colorsArray = [
    ...redToYellow,
    ...yellowToGreen,
    ...greenToYellow,
    ...yellowToRed,
  ];
  const getNeedleColor = (percentage, colors) => {
    const index = Math.min(
        Math.floor(percentage * colors.length),
        colors.length - 1
    );
    return colors[index];
  };
  const needleColor = getNeedleColor(percentage, colorsArray);

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "100%",
      }}
    >
      <div>
        <GaugeChart
          id="gauge-chart2"
          nrOfLevels={26}
          colors={colorsArray}
          arcWidth={0.3}
          percent={percentage}
          needleColor={needleColor}
          style={{ minHeight: "150px", width: "100%" }}
        />
      </div>
      <div className="text">
        <div className="Min-Max ">
          <span>
            {minValue} <span>&#8451;</span>
          </span>
          <span>
            {currentValue} <span>&#8451;</span>
          </span>
          <span>
            {maxValue} <span>&#8451;</span>
          </span>
        </div>
      </div>
    </div>
  );
};
function generateGradientColor(startColor, endColor, steps) {
  const stepFactor = 1 / (steps - 1);
  const interpolatedArray = [];
  for (let i = 0; i < steps; i++) {

    const interpolatedColor = interpolateColor(startColor, endColor, stepFactor * i);
    interpolatedArray.push(interpolatedColor);
  }
  return interpolatedArray;

}
function interpolateColor(color1, color2, factor) {
  const r1 = parseInt(color1.substring(1, 3), 16);
  const g1 = parseInt(color1.substring(3, 5), 16);
  const b1 = parseInt(color1.substring(5, 7), 16);

  const r2 = parseInt(color2.substring(1, 3), 16);
  const g2 = parseInt(color2.substring(3, 5), 16);
  const b2 = parseInt(color2.substring(5, 7), 16);

  const r = Math.round(r1 + (r2 - r1) * factor).toString(16).padStart(2, '0');
  const g = Math.round(g1 + (g2 - g1) * factor).toString(16).padStart(2, '0');
  const b = Math.round(b1 + (b2 - b1) * factor).toString(16).padStart(2, '0');

  return `#${r}${g}${b}`;
}

const getNeedleColor = (percentage, colors) => {
  const index = Math.min(
      Math.floor(percentage * colors.length),
      colors.length - 1
  );
  return colors[index];
};

export default Gauge;
